@import "../../styles/base";

.bootcamp {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $primary-color;
  color: $accent-color;

  .page {
    min-height: 100vh;
  }

  .bg-black {
    background-color: $accent-color;
    color: $primary-color;
    box-shadow: 0px 4px 8px 8px #00000020;
  }

  .category {
    padding: 1em;

    .card {
      background-color: $accent-color;
      color: $primary-color;
      border-radius: 2px;
      padding: 1em;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  iframe {
    width: 100%;
  }
}
